@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Roboto:wght@400;500;700&display=swap");

html {
  font-family: Roboto, sans-serif;
}

body,
h1,
p {
  margin: 0;
  padding: 0;
}

.card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
}

.card__image-block {
  display: flex;
  margin: 32px 19px 40px;
}

.card__image {
  width: 160px;
  height: 134px;
}

.card__name-block {
  margin: 0 16px 4px;
}

.card__name {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #060b35;
}

.card__code-block {
  margin: 0 84px 16px 16px;
}

.card__code {
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #616070;
}

.card__stars-block {
  margin: 0 16px 24px;
  display: flex;
  justify-content: space-between;
}

.card__reviews {
  font-weight: 14px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
  align-items: flex-end;
}

.stars {
  display: flex;
}

.stars__star {
  height: 16px;
  width: 16px;
  background-image: url(images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n + 4) {
  background-image: url(images/star-active.svg);
}

.card__price-block {
  margin: 0 16px 16px;
  display: flex;
  justify-content: space-between;
}

.card__price {
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  display: flex;
  align-items: flex-end;
  color: #616070;
}

.card__amount {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  color: #060b35;
}

.card__button-block {
  margin: 0 16px 16px;
}

.card__button {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #fff;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  height: 40px;
  width: 166px;
  background: #00acdc;
  border-radius: 5px;
}

.card__button:hover {
  background-color: white;
  color: #00acdc;
  border: 1px solid #00acdc;
}
